#page-hackathon {
  background: #333;
  text-align: center;
  // height: 100vh;
  // width: 100vw;
  padding-top: 5vh;
  color: #fff;
  text-align: -webkit-center;

  #items {
    display: flex;
    overflow-x: auto;
    // overflow: scroll;
    scroll-snap-type: x mandatory;
    -webkit-overflow-scrolling: touch;
    scroll-behavior: smooth;
    border: groove;
    width: 98vw;
    
    .item {
      flex: none;
      width: 100vw;
      height: auto;
      scroll-snap-align: start;
      pointer-events: none;
    }
  
    img {
      height: 70vh;
      width: 80vw;
      margin: 10px;
      // object-fit: cover;
    }
  }
}